.Form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  &_section {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 70px;
  }

  &_buttons {
    display: flex;
    flex-direction: row;
    padding-bottom: 43px;
  }

  &_button {
    height: 34px;
    width: 106px;
    font-style: 14px;
    font-weight: bold;
    border: solid 2px #363C5C;
    cursor: pointer;
    color: #363C5C;
    background-color: #fff;
  }

  &_button:hover {
    background-color: #363C5C;
    color: #fff;
  }

  &_button-active {
    background-color: #363C5C;
    color: #fff;
  }

  &_button-dark {
    border-right: 0;
    border-radius: 6px 0 0 6px;
  }

  &_button-light {
    border-radius: 0 6px 6px 0;
  }

  &_title {
    font-style: 60px;
    font-weight: bold;
    color: #363C5C;
    padding-bottom: 30px;
  }

  &_columns {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  &_column {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  &_p {
    padding-bottom: 40px;
  }

  &_section-button {
    padding: 40px 0 70px 0;
  }

  &_button-submit {
    font-weight: bold;
    font-size: 18px;
    color: #F1E2EC;
    width: 265px;
    height: 65px;
    border-radius: 6px;
    border: 0;
    background: linear-gradient(to right, #D84758, #8A6BA6);
    box-shadow: 0px 0px 10px 8px #EDCBD6;
    transition: transform .3s;
    cursor: pointer;

    &:hover {
    background: linear-gradient(to right, #8A6BA6, #D84758);
    }

    &:disabled {
      background: linear-gradient(to right, aliceblue, aliceblue);
    }
  }
}
