.MoneyHelp {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 15px 20px 15px;
  box-sizing: border-box;
  align-items: center;

  &_paytment {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &_container {
    width: 490px;
    height: 156px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
  }

  &_title {
    color: #B9BABE;
    font-size: 15px;
    padding-bottom: 10px;
  }
}
