.Help {
  display: flex;
  flex-direction: column;

  &_content {
    width: 100%;
    background-color: #fff;
    border: solid 2px #C8D5E5;
    border-radius: 12px;
    padding: 30px;
  }

  &_container {
    border: 0;
    background-color: #fff;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 20px;
    padding-bottom: 15px;
  }
}
