.Payment {
  width: 150px;
  height: 70px;
  border-radius: 12px;
  border: 0;
  padding: 18px 18px 12px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: #C8D5E5;
  font-size: 12px;
  font-weight: bold;
  color: #FBFDF9;
  cursor: pointer;

  &-active, &:hover {
    background-color: #936397;
  }

  &_title {
    font-size: 8px;
    color: #FBFDF9;
  }
}
