.HelpItem {
  border: 0;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  cursor: pointer;

  &:hover {
    box-shadow: 0px 8px 14px rgba(54, 60, 92, 0.48);
    border-radius: 8px;
  }

  &_icon {
    height: 70px;
    width: 70px;
    border-radius: 12px;
    border: solid 3px #C8D5E5;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    &-active, &:hover {
      &::after {
        content: '';
        position: absolute;
        width: 20px;
        aspect-ratio: 1;
        background-color: #fff;
        border: solid 1px #C8D5E5;
        border-right: 0;
        border-bottom: 0;
        left: 50%;
        top: 115%;
        transform: translateX(-50%) rotate(45deg);
      }
    }
  }

  &_img {
    height: 40px;
    width: 40px;
  }

  &_title {
    font-size: 14px;
    font-weight: bold;
    color: #929596;
  }
  
  &_title-active, &_title:hover {
    color: #363C5C;
  }
  
  &_icon-active {
    background-color: #936397;
    border-color: #936397;
  }
}
