.Card {
  width: 340px;
  height: 156px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  background-color: #C8D5E5;
  border-radius: 12px;

  &_input {
    height: 30px;
    width: 65px;
    border: 0;
    background-color: #F3F6FB;
    border-radius: 3px;
    display: flex;
    align-items: center;
    padding-left: 10px;
    box-sizing: border-box;

    &::placeholder {
      color: #cbced2;
    }

    &:focus {
      border: 1px;
    }
  }

  &_title {
    font-size: 8px;
    color: #8e9297;
  }

  &_number {
    display: flex;
    flex-direction: column;
  }

  &_number-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  &_data {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  &_CVV, &_expiryDate {
    display: flex;
    flex-direction: column;
  }
}
