.Field {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  color: #929596;
  gap: 5px;

  &_input {
    height: 34px;
    border: 0;
    background-color: #F3F6FB;
    border-radius: 3px;
    padding-left: 10px;
    box-sizing: border-box;

    &::placeholder {
      color: #cfd0d1;
    }

    &:focus {
      border: 0;
      color: #363C5C;
    }
  }
}
